import { Component } from '@angular/core';
import { HtmlParser } from '@angular/compiler';
import {FormControl, FormGroup} from '@angular/forms';
import { DataService } from './data.service';
import { trigger, state, style, transition, animate, keyframes} from '@angular/animations';

@Component({
  selector: 'app-root',
  templateUrl: './creations-library/creations-library.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [
    trigger('myAwesomeAnimation', [
      state('small', style({
        transform: 'scale(1)',
      })),
      state('large', style({
        transform: 'scale(1.2)',
      })),
      transition('small <=> large', animate('500ms ease-in', keyframes([
        style({ opacity: 1, transform:  'rotate(0deg) translate(0%, 10%)'}),
        style({ opacity: 0.5, transform:  'rotate(90deg) translate(-10%, 0%)'}),
        style({ opacity: 0, transform:  'rotate(180deg) translate(0%, -10%)'}),
        style({ opacity: 0.5, transform:  'rotate(270deg) translate(-10%, 0%)'}),
        style({ opacity: 1, transform:  'rotate(360deg) translate(0%, 10%)'}),
      ]))),  
    ])

  ]
})
export class AppComponent {

  state: string = 'small';

  constructor(public dataService:DataService){
  }

  imgLarger(){ 
    this.state = (this.state === 'small' ? 'large' : 'small');
  }

  favoriteCars:string = '';
  ngOnInit() {
    this.favoriteCars = this.dataService.favoriteCarsTypes();
  }
  male = true;
  human = true;
  toHuman($event){
    this.human = true;
  }
  toDog($event){
    this.human = false;
  }
  toMale($event){
    this.male = true;
  }
  toFemale($event){
    this.male = false;
  }
  bgClrMain = 'transparent';
  toBG($event, bgClr){
    console.log(bgClr);
    this.bgClrMain = bgClr;
  }
BGandBGC = [
  {
    value: "Default",
    BGC: "trnsp-bg",
    BG: 'transparent'
  },
  {
    value: "Green",
    BGC: "green-bg",
    BG: '#6dd17a'
  },
  {
    value: "Yellow",
    BGC: "yellow-bg",
    BG: '#bfd16d'
  },
  {  
    value: "Red",
    BGC: "red-bg",
    BG: '#c78585'
  },
  {
    value: "Blue",
    BGC: "blue-bg",
    BG: "#6db2d1"
  },  
]
  manClass = 'man';
  womanClass = 'woman';
  petClass = 'pet';
  editBtn(event){
   console.log(event);//"Edit button mouseenter"
  }
  backBtn(event){
    console.log(event);//"Back button click"
  }
  btnEditStatus = true;
  btnBackStatus = false;
  manImg = "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSShYN0gnvg73V4s9tTqd-YlDEYRpW3ok4_yFjlF9ZqwHBn1gRpMA";
  womanImg = "data:image/png;base64,";
  dogMImg = "data:image/png;base64,";
  dogWImg = "https://cdn4.iconfinder.com/data/icons/tail-waggers/120/bows-512.png";
  dogArrM = ['dog', 'male'];
  dogArrW = ['dog', 'female'];
  womanArr = ['human', 'female'];
  manArr = ['human', 'male'];
  dogObject = {
    rase: 'dog',
    gender: {
      male: 'male',
      female: 'female',
    }
  }
  humanObject = {
    rase: 'human',
    gender: {
      male: 'male',
      female: 'female',
    }
  }
}



